.table-tasks table {
  width: 100%;
}
.table-tasks td {
  border-spacing: 0px;
  height: 30px;
  padding: 0 3px 0 3px;
  height: 50px;
}
.table-tasks th {
  border-spacing: 0px;
  height: 30px;
  padding: 0 3px 0 3px;
}
.table-tasks th:not(:nth-child(2)) {
  text-align: right;
}
.table-tasks th:nth-child(2) {
  text-align: left;
}
.table-tasks th:first-child {
  text-align: left;
  width: 30px;
}
.table-tasks td:not(:nth-child(2)) {
  text-align: right;
}
.table-tasks td:nth-child(2) {
  text-align: left;
}
.table-tasks td:first-child {
  text-align: left;
  width: 30px;
}
.table-tasks th md-checkbox {
  margin-bottom: 0px;
}

