.table-config table {
  width: 100%;
}
.table-config td {
  border-spacing: 0px;
  height: 30px;
  padding: 0 3px 0 3px;
  vertical-align: top;
}
.table-config th {
  border-spacing: 0px;
  height: 30px;
  padding: 0 3px 0 3px;
}
.table-config tr:first-child td {
  padding-top: 10px;
}
.table-config td:first-child {
  text-align: right;
  font-weight: bold;
  width: 150px;
}
.table-config td:last-child {
  text-align: left;
}
.table-config tr:first-child td:first-child {
  border-radius: 10px 0 0 0;
}
.table-config tr:first-child td:last-child {
  border-radius: 0 10px 0 0;
}
.table-config tr:last-child td:first-child {
  border-radius: 0 0 0 10px;
}
.table-config tr:last-child td:last-child {
  border-radius: 0 0 10px 0;
}
.table-config pre {
  background: #F8F8FF;
  border: black dashed 1px;
